div
  ion-page 
    background-color: var(--ion-background-color)
  ion-header 
    --background: var(--ion-background-color)
  ion-toolbar 
    --background: var(--ion-background-color)
  ion-content 
    --background: var(--ion-background-color)

ion-button 
  &.white
    font-weight: normal
    box-shadow: none
    --background: var(--ion-background-color)
    --background-activated: var(--ion-background-color)
    color: var(--ion-color-primary)
@import ../../theme/ui-rules.sass
.signin-page
  ion-content 
    .login-entry
      width: 100%
      padding: 0 $grid*12 4vh
      display: flex
      flex-direction: column
    .login-data
      padding-top: var(--ion-safe-area-top)
      flex-direction: column
      display: flex
      justify-content: flex-start
      align-items: flex-start
      padding: 0 $grid*8 4vh
  ion-footer 
    display: block
    max-width: 100%
    padding: 0