div
  ion-page 
    background-color: var(--ion-background-color)
  ion-header 
    --background: var(--ion-background-color)
  ion-toolbar 
    --background: var(--ion-background-color)
  ion-content 
    --background: var(--ion-background-color)

ion-button 
  &.white
    font-weight: normal
    box-shadow: none
    --background: var(--ion-background-color)
    --background-activated: var(--ion-background-color)
    color: var(--ion-color-primary)
.task-card
    margin-top: 16px
    box-shadow: 0px 0px 24px rgb(0 0 0 / 8%)
    border-radius: 24px

    ion-img::part(image)
        width: auto    
           
.task-card::part(native)
    margin: 8px 0px
    height: 80px



